import React from 'react';
import * as microsoftTeams from "@microsoft/teams-js";
import querystring from 'querystring';
import './../App.css';

function getsevertoken(context, tokens) {
  var tid = context.tid;
  var token = tokens;
  var scopes = ["https://graph.microsoft.com/User.Read"];

  return  new Promise((resolve, reject) => {
      const url = "https://login.microsoftonline.com/" + tid + "/oauth2/v2.0/token";
      const params = {
          client_id: '630f584b-b191-409e-a327-e0d185632359',
          client_secret: '91M_kPSR6h4-jhGl7disIg_bwhNcBXvAS~',
          grant_type: "urn:ietf:params:oauth:grant-type:jwt-bearer",
          assertion: token,
          requested_token_use: "on_behalf_of",
          scope: scopes.join(" ")
      };
  
      fetch(url, {
        method: "POST",
        body: querystring.stringify(params),
        headers: {
          Accept: "application/json",
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(result => {
        if (result.status !== 200) {
          result.json().then(json => {
            // TODO: Check explicitly for invalid_grant or interaction_required
            reject({"error":json.error});
          });
        } else {
          result.json().then(json => {
            resolve(json.access_token);
          });
        }
      });
  });

}


function getClientSideToken() {

  return new Promise((resolve, reject) => {

    console.log("1. Get auth token from Microsoft Teams");

    microsoftTeams.authentication.getAuthToken({
      successCallback: (result) => {
        //display(result)
        //console.log(result);
        resolve(result);
      },
      failureCallback: function (error) {
        reject("Error getting token: " + error);
        console.log('Error getting token');
      }
    });

  });

}

// 2. Exchange that token for a token with the required permissions
//    using the web service (see /auth/token handler in app.js)
function getServerSideToken(clientSideToken) {

  //display("2. Exchange for server-side token");

  return new Promise((resolve, reject) => {

    microsoftTeams.getContext((context) => {

      getsevertoken(context,clientSideToken)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            reject(response.error);
          }
        })
        .then((responseJson) => {
          if (responseJson.error) {
            reject(responseJson.error);
          } else {
            const serverSideToken = responseJson;
            //display(serverSideToken);
            resolve(serverSideToken);
          }
        });
    });
  });
}


class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      client_token: 'client token',
      server_token: 'server token',
      profile: {},
      theme: 'theme',
      name: 'name',
      context :{}
    }

  }

  componentDidMount() {
   
    let currentComponent = this;

    microsoftTeams.initialize();

    microsoftTeams.getContext(function (context) {

      if (context && context.theme) {

        console.log(context);
  
        currentComponent.setState({
            theme: 'defualt',
            context : context        
        });
      } else {
        console.log('no theme');
      }
    });



  };


  gettokenDetails(){
    
    getClientSideToken()
    .then((clientSideToken) => {

      this.setState((prevState, props) => {
        return {client_token: clientSideToken}
      })
  
      return getServerSideToken(clientSideToken);
    })
    .then((serverSideToken) => {

      console.log(serverSideToken);

      this.setState((prevState, props) => {
        return {server_token: serverSideToken}
      })
      
    })
    .catch((err) => {
      console.log(err);
      alert('you are not in the MS teams App');
    })
  }

  render() {
    return (
      <div> 
        <div className="container">
        <h5 className="text-left">Demo tokens and profile</h5>

        </div>
        

        <div className="container">

          <div className="row">          
            <div className="col-sm-3">
              Theme
          </div>
            <div className="col-sm-9">
            {this.state.theme} , {this.state.name}
          </div>         
          </div>
     
     
     <br/>
     
          <div className="container">
          <h5 className="text-left">Profile</h5>
          </div>
     
          <div className="row">          
            <div className="col-sm-3">
            <p>Name:</p>
            <p>Email:</p>
            <p>id:</p>
          </div>
            <div className="col-sm-9">
            <p>{this.state.context.teamSiteDomain}</p>
            <p>{this.state.context.upn}</p>
            <p>{this.state.context.tid}</p>
          </div>         
          </div>
     
          <button onClick={this.gettokenDetails.bind()}>Get token</button>
          <div className="row">          
            <div className="col-sm-3">
            client token
          </div>
            <div className="col-sm-9">
            {this.state.client_token}
          </div>         
          </div>
     
          <div className="row">          
            <div className="col-sm-3">
            server token
          </div>
            <div className="col-sm-9">
            {this.state.server_token}
          </div>         
          </div>
        </div>
      </div>
    );
  }
}

export default App;